@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700&display=swap);
.image-header{position:relative;height:33vh;overflow:hidden}.image-header img{position:relative;max-height:33vh;width:100%;object-fit:cover}.image-header .content{position:absolute;bottom:0;right:0;margin:1rem 0.5rem;text-align:end;color:white;z-index:5}.image-header .content .temperature .number{font-size:3rem}.image-header .content .temperature .unit{font-size:2.5rem}.image-header .content .weather{font-size:2rem;font-weight:bold;text-transform:uppercase}@media screen and (min-width: 768px){.image-header img{border-radius:1rem 1rem 0 0}}

.city-form{align-self:center;margin:1rem}.city-form .error{color:white}.city-form .error .fa-exclamation-circle{margin:0 0.5rem}.city-form form{display:flex;flex-direction:row;justify-items:center;align-items:center;border-radius:0.5rem;background-color:#2a435c;padding:1rem}.city-form input{background-color:#2a435c;color:white;text-align:center;font-size:1.25rem}.city-form input::-webkit-input-placeholder{opacity:1;color:white}.city-form input:-ms-input-placeholder{opacity:1;color:white}.city-form input::-ms-input-placeholder{opacity:1;color:white}.city-form input::placeholder{opacity:1;color:white}.city-form input:focus::-webkit-input-placeholder{visibility:hidden}.city-form input:focus:-ms-input-placeholder{visibility:hidden}.city-form input:focus::-ms-input-placeholder{visibility:hidden}.city-form input:focus::placeholder{visibility:hidden}.city-form button,.city-form button i{background-color:#2a435c;color:white}

.unit-control{margin:auto 1rem 1rem auto}.unit-control .toggle-symbol{height:100%;display:flex;flex-direction:row;justify-content:center;align-items:center}

.extended-data{color:white;margin:1rem}.extended-data .location{margin-bottom:0.5rem}.extended-data .location span{font-weight:300}.extended-data .location .fas{margin-right:0.5rem}.extended-data .weather-description{text-transform:uppercase;font-weight:700;font-size:1.75rem;margin:0.75rem 0}.extended-data .humidity,.extended-data .pressure{margin:0.75rem 0}.extended-data .humidity .value,.extended-data .pressure .value{font-size:2rem;font-weight:300}

*{padding:0;margin:0;border:none;box-sizing:border-box}html,body,#root,main,.App{width:100%;height:100%}body,input{font-family:'Montserrat', sans-serif}.App{color:white;display:flex;flex-direction:column;justify-content:flex-start;background-color:#19242f}.about{text-align:center;margin-bottom:0.5rem}@media screen and (min-width: 768px){main{background:-webkit-gradient(linear, left top, left bottom, from(#364054), to(#1c2b39));background:linear-gradient(180deg, #364054 0%, #1c2b39 100%)}.App{position:absolute;bottom:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);margin:0 auto;max-width:500px;height:95%;border-radius:1rem 1rem 0 0}}

